


































































































.virus-index{
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url(../../assets/imgs/virus/bg.png);
  background-size: 100% 100%;

  .bottom{
    width: 1210px;
    height: 350px;
    position: absolute;
    bottom: 30px;
    left: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      height: 60px;
      line-height: 60px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
  }
  .bottom-left{
    width: 1200px;
    color: #fff;
    position: absolute;
    bottom: 0;
    .bottom-title{
      color: #b6f0f2;
      text-align: center;
      height: 40px;
      line-height: 40px;
      font-size: 20px;
      font-weight: 600;
    }
    .bottom-back{
      background: url(../../assets/imgs/index/wangge.png);
      box-sizing: border-box;
      padding: 13px 8px;
    }
    ul{
      padding: 12px 0;
      li{
        font-size: 18px;
        &:hover{
          background: #324097;
          cursor: pointer;
        }
        height: 30px;
        line-height: 30px;
        color: #8BAFBA;
        box-sizing: border-box;
        padding-left: 40px;
        span{
          display: inline-block;
          width: 2px;
          height: 20px;
          background: #ff7a01;
          margin-right: 10px;
          margin-top: 5px;
          vertical-align: top;
        }
        p{
          display: inline-block;
          margin-left: 10px;
          color: #D8DFFF;
          vertical-align: top;
        }
      }
    }
  }

  .bottom-right{
    width: 600px;
    height: 350px;
    position: absolute;
    bottom: 30px;
    right: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      height: 60px;
      line-height: 60px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
  }

  .top-right{
    width: 440px;
    height: 400px;
    position: absolute;
    top: 120px;
    right: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      height: 60px;
      line-height: 60px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
  }

  .top-center{
    width: 520px;
    height: 400px;
    position: absolute;
    top: 120px;
    left: 55%;
    margin-left: -260px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      position: absolute;
      width: 100%;
      height: 80px;
      line-height: 80px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      >span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
      >span:nth-child(2){
        span{
          font-weight: bold;
          font-size: 20px;
        }
      }
    }
  }

  .top-left{
    width: 700px;
    height: 400px;
    position: absolute;
    top: 120px;
    left: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      position: absolute;
      height: 60px;
      line-height: 60px;
      color: #fff;
      span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
    .top{
      width: 100%;
      height: 80px;
      line-height: 80px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      .item{
        display: inline-block;
        margin: 0 20px 0 30px;
        p{
          display: inline-block;
          span{
            font-weight: bold;
            font-size: 25px;
          }
        }
        >span{
          display: inline-block;
          width: 10px;
          height: 10px;
          border-radius: 50px;
          background: #76FBFD;
          margin-right: 10px;
        }
      }
    }
  }

  >.title{
    position: absolute;
    top: 0;
    width: 100%;
    height: 80px;
    text-align: center;
    color: #f6f6f8;
    background: url(../../assets/imgs/index/head.png) no-repeat;
    background-size: 100%;
    background-position: bottom center;
    z-index: 10;
    .txt-center{
          h1{
            font-size: 35px;
            font-weight: 400;
          }
          p{
            font-size: 14px;
            font-weight: 400;
          }
        }
  }
}
