.virus-index {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url(../../assets/imgs/virus/bg.png);
  background-size: 100% 100%;
}
.virus-index .bottom {
  width: 1210px;
  height: 350px;
  position: absolute;
  bottom: 30px;
  left: 30px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
}
.virus-index .bottom .title {
  height: 60px;
  line-height: 60px;
  color: #fff;
  border-bottom: 1px solid #053A93;
}
.virus-index .bottom .title span:nth-child(1) {
  position: relative;
  top: 3px;
  margin-left: 30px;
  display: inline-block;
  width: 5px;
  height: 20px;
  background: #76FBFD;
  border-radius: 5px;
  margin-right: 10px;
}
.virus-index .bottom-left {
  width: 1200px;
  color: #fff;
  position: absolute;
  bottom: 0;
}
.virus-index .bottom-left .bottom-title {
  color: #b6f0f2;
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  font-weight: 600;
}
.virus-index .bottom-left .bottom-back {
  background: url(../../assets/imgs/index/wangge.png);
  box-sizing: border-box;
  padding: 13px 8px;
}
.virus-index .bottom-left ul {
  padding: 12px 0;
}
.virus-index .bottom-left ul li {
  font-size: 18px;
  height: 30px;
  line-height: 30px;
  color: #8BAFBA;
  box-sizing: border-box;
  padding-left: 40px;
}
.virus-index .bottom-left ul li:hover {
  background: #324097;
  cursor: pointer;
}
.virus-index .bottom-left ul li span {
  display: inline-block;
  width: 2px;
  height: 20px;
  background: #ff7a01;
  margin-right: 10px;
  margin-top: 5px;
  vertical-align: top;
}
.virus-index .bottom-left ul li p {
  display: inline-block;
  margin-left: 10px;
  color: #D8DFFF;
  vertical-align: top;
}
.virus-index .bottom-right {
  width: 600px;
  height: 350px;
  position: absolute;
  bottom: 30px;
  right: 30px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
}
.virus-index .bottom-right .title {
  height: 60px;
  line-height: 60px;
  color: #fff;
  border-bottom: 1px solid #053A93;
}
.virus-index .bottom-right .title span:nth-child(1) {
  position: relative;
  top: 3px;
  margin-left: 30px;
  display: inline-block;
  width: 5px;
  height: 20px;
  background: #76FBFD;
  border-radius: 5px;
  margin-right: 10px;
}
.virus-index .top-right {
  width: 440px;
  height: 400px;
  position: absolute;
  top: 120px;
  right: 30px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
}
.virus-index .top-right .title {
  height: 60px;
  line-height: 60px;
  color: #fff;
  border-bottom: 1px solid #053A93;
}
.virus-index .top-right .title span:nth-child(1) {
  position: relative;
  top: 3px;
  margin-left: 30px;
  display: inline-block;
  width: 5px;
  height: 20px;
  background: #76FBFD;
  border-radius: 5px;
  margin-right: 10px;
}
.virus-index .top-center {
  width: 520px;
  height: 400px;
  position: absolute;
  top: 120px;
  left: 55%;
  margin-left: -260px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
}
.virus-index .top-center .title {
  position: absolute;
  width: 100%;
  height: 80px;
  line-height: 80px;
  color: #fff;
  border-bottom: 1px solid #053A93;
}
.virus-index .top-center .title > span:nth-child(1) {
  position: relative;
  top: 3px;
  margin-left: 30px;
  display: inline-block;
  width: 5px;
  height: 20px;
  background: #76FBFD;
  border-radius: 5px;
  margin-right: 10px;
}
.virus-index .top-center .title > span:nth-child(2) span {
  font-weight: bold;
  font-size: 20px;
}
.virus-index .top-left {
  width: 700px;
  height: 400px;
  position: absolute;
  top: 120px;
  left: 30px;
  background: url(../../assets/imgs/index/dibu.png);
  background-size: 100% 100%;
}
.virus-index .top-left .title {
  position: absolute;
  height: 60px;
  line-height: 60px;
  color: #fff;
}
.virus-index .top-left .title span:nth-child(1) {
  position: relative;
  top: 3px;
  margin-left: 30px;
  display: inline-block;
  width: 5px;
  height: 20px;
  background: #76FBFD;
  border-radius: 5px;
  margin-right: 10px;
}
.virus-index .top-left .top {
  width: 100%;
  height: 80px;
  line-height: 80px;
  color: #fff;
  border-bottom: 1px solid #053A93;
}
.virus-index .top-left .top .item {
  display: inline-block;
  margin: 0 20px 0 30px;
}
.virus-index .top-left .top .item p {
  display: inline-block;
}
.virus-index .top-left .top .item p span {
  font-weight: bold;
  font-size: 25px;
}
.virus-index .top-left .top .item > span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50px;
  background: #76FBFD;
  margin-right: 10px;
}
.virus-index > .title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 80px;
  text-align: center;
  color: #f6f6f8;
  background: url(../../assets/imgs/index/head.png) no-repeat;
  background-size: 100%;
  background-position: bottom center;
  z-index: 10;
}
.virus-index > .title .txt-center h1 {
  font-size: 35px;
  font-weight: 400;
}
.virus-index > .title .txt-center p {
  font-size: 14px;
  font-weight: 400;
}
